import React, { useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
import { useDrop } from 'react-dnd';
import { RootState } from '../../modules';
import { returnCoin } from '../../modules/calc';
import { CoinType } from '../../constants/ItemTypes';
import DisplayPrice from '../common/Middle/DisplayPrice';
import ReturnIo from '../common/Middle/ReturnIo';

const MiddleWrap = styled.div`
    display: flex;
    margin-top: 15px;
    max-width: 440px;
`;

const BigIconBx = styled.div`
    position: relative;
    width: 50%;
    border-top: 5px solid #185c0a;
    border-left: 5px solid #185c0a;
    border-right: 2px solid #185c0a;
    border-bottom: 1px solid #185c0a;
    border-radius: 10px;
    background: rgb(255, 255, 255);
    background: linear-gradient(
        160deg,
        rgba(255, 255, 255, 1) 20%,
        rgb(34, 125, 15) 40%,
        rgba(20, 117, 0, 1) 60%,
        rgba(255, 255, 255, 1) 80%
    );

    &::before,
    &::after {
        content: '';
        display: block;
        position: absolute;
        background: rgb(255, 255, 255);
        background: linear-gradient(
            160deg,
            rgba(255, 255, 255, 1) 15%,
            rgba(135, 204, 120, 1) 35%,
            rgba(58, 189, 31, 1) 50%,
            rgba(135, 204, 120, 1) 65%,
            rgba(255, 255, 255, 1) 85%
        );
        clip-path: polygon(
            50% 0%,
            67% 26%,
            98% 35%,
            78% 60%,
            79% 91%,
            50% 79%,
            21% 91%,
            22% 60%,
            2% 35%,
            33% 28%
        );
    }

    &::before {
        top: 15px;
        left: 15px;
        width: 20%;
        height: 27%;
        transform: rotate(20deg) skew(353deg);
    }

    &::after {
        bottom: 15px;
        right: 15px;
        width: 14%;
        height: 17%;
        transform: rotate(-40deg) skew(10deg);
    }
`;

const BigIcon = styled.div`
    padding-bottom: 85%;
`;

const IconTxt = styled.span`
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) skew(-10deg) rotate(-20deg);
    color: #fff;
    font-family: 'Righteous', cursive;
    font-size: 2rem;
    text-shadow: 2px 2px 2px #333;
`;

const InsertBx = styled.div`
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    width: 50%;
`;

function Middle({ story }: { story: boolean }) {
    if (!story) {
        const coinAmount = useSelector((state: RootState) => state.calc.machineCoin);
        const dispatch = useDispatch();

        const ReturnEvent = () => dispatch(returnCoin());
        const [{ isOver }, drop] = useDrop({
            accept: CoinType.COIN,
            drop: () => ({ name: '!' }),
            collect: (monitor) => ({
                isOver: !!monitor.isOver(),
            }),
        });
        return (
            <MiddleWrap>
                <BigIconBx>
                    <BigIcon>
                        <IconTxt>
                            Chilsung
                            <br />
                            Cider
                        </IconTxt>
                    </BigIcon>
                </BigIconBx>
                <InsertBx>
                    <DisplayPrice coinAmount={coinAmount} />
                    <ReturnIo returnEvent={ReturnEvent} drop={drop} />
                </InsertBx>
            </MiddleWrap>
        );
    } else {
        return (
            <MiddleWrap>
                <BigIconBx>
                    <BigIcon>
                        <IconTxt>
                            Chilsung
                            <br />
                            Cider
                        </IconTxt>
                    </BigIcon>
                </BigIconBx>
                <InsertBx>
                    <DisplayPrice coinAmount={0} />
                    <ReturnIo returnEvent={false} drop={false} />
                </InsertBx>
            </MiddleWrap>
        );
    }
}

export default Middle;
